.navbar {
  /*padding: 20px 20px;*/
  background-color: rgb(57, 53, 54);
  overflow: auto;
}

.welcome {
  overflow: auto;
  float: left;
  padding: 22px 20px;
}

.welcomeIcon {
  -ms-flex-item-align: center;
      align-self: center;
  height: 30px;
  border: 3px solid #FFCC00;
  border-radius: 100px;
  margin-right: 10px;
  float: left;
  line-height: 36px;
}

.welcomeText {
  float: left;
  color: white;
  line-height: 36px;
}

.menu {
  float: right;
  padding-right: 30px;
}

.menuItem {
  float: left;
  padding: 0 10px;
  line-height: 80px;
  height: 80px;
}

.menuIcon {
  height: 16px;
  padding: 32px 0;
  float: left;
  margin-right: 5px;
}

.menuText {
  float: left;
  color: white;
  line-height: 80px;
  font-size: 0.8em;
}

.menuLogout {
  background-color: #FFCC00;
  padding: 0 30px;
}

.menuLogout .menuText {
  color: black;
}

.links {
  clear: both;
  height: 50px;
  background-color: #ddd;
}

.linksLogo {
  height: 30px;
  padding: 10px 20px;
  float: left;
}

.link {
  line-height: 30px;
  padding: 10px 50px;
  float: left;
  font-size: 0.8em;
  color: #333;
}

.link:hover {
  font-weight: bold;
  border-top: 5px solid #54C2E8;
  padding: 5px 50px 10px 50px;
}

.linkActive {
  font-weight: bold;
  background-color: white;
  border-top: 5px solid #FFCC00;
  padding: 5px 50px 10px 50px;
}

.section {
  padding: 20px;
  overflow: auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.sectionTitle {
  font-family: "Aachen", sans-serif;
  font-size: 1.7em;
  color: #333;
}



