.IND-backIcon {
  height: 30px;
  float:left;
}

.IND-backText {
  font-size: 0.8em;
  line-height: 30px;
  color: #FFCC00;
  font-weight: 400;
}

.IND-details-col {
  width: 50%;
  float: left;
}

.IND-details {
  padding: 10px 0;
  overflow: auto;
}

.IND-detail {
  width: 20%;
  float: left;
}

.IND-detail-label {
  font-family: "HelveticaBold", sans-serif;
  margin-bottom: 10px;
  font-size: 0.8em;
}

.IND-detail-value {
  /*font-size: 1.2em;*/
  font-family: "HelveticaLt", sans-serif;
}

.IND-table {
  width: 100%;
  margin: 10px 0;
  font-size: 0.8em;
  border-spacing: 0;
}

.IND-tableHeader {
  border-top: 2px solid #FFCC00;
  /*border-bottom: 1px solid #eee;*/
}

.IND-tableHeader {
  padding: 20px 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: "HelveticaBold", sans-serif;
  text-align: left;
}

.IND-tableRow {
  padding: 20px 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-top: 1px solid #eee;
  font-family: "HelveticaLt", sans-serif;
  /*display: flex;
  justify-content: center;*/
}

.IND-totalRow > td {
  border-top: 2px solid #777;
  border-bottom: 0;
  font-family: "HelveticaBold", sans-serif;
}



