.Login-navbar {
  padding: 10px 20px;
  background-color: rgb(57, 53, 54);
  text-align: center;
}

.Login-body {
  /*width: 1200px;*/
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.Login-form {
  -ms-flex-item-align: center;
      align-self: center;
  text-align: center;
  padding: 40px;
}

.Login-userIcon {
  border: 5px solid #333;
  border-radius: 100px
}

.Login-label {
  margin: 10px 0;
  text-align: left;
  color: #777;
}

.Login-bottom {
  padding: 20px 0;
}

.Login-forgotPassword {
  float: left;
  font-size: 0.8em;
  text-decoration: underline;
  cursor: pointer;
}

.Login-button {
  float: right;
  background-color: #FFCC00;
  padding: 15px 70px;
  border-radius: 10px;
  font-family: "HelveticaBold", sans-serif;
  -webkit-box-shadow: 1px 1px 1px grey;
          box-shadow: 1px 1px 1px grey;
  cursor: pointer;
}

.textbox {
  padding: 10px;
  background-color: white;
  border: 1px solid #ddd;
  width: 500px;
}



