.SO-table {
  width: 100%;
  margin: 10px 0;
  font-size: 0.8em;
  border-spacing: 0;
  border-bottom: 2px solid #777;
}

.SO-tableHeader {
  border-top: 2px solid #FFCC00;
}

.SO-tableHeader {
  padding: 20px 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: "HelveticaBold", sans-serif;
  text-align: left;
}

.SO-tableRow {
  padding: 30px 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-top: 1px solid #eee;
  font-family: "HelveticaLt", sans-serif;
  /*display: flex;
  justify-content: center;*/
}

.SO-tableItem {
  /*align-self: center;
  word-wrap: break-word;*/
}

.SO-details {
  white-space: nowrap;
}

.SO-detailsText {
  color: #54c2e8;
  display: inline;
  white-space: nowrap;
  float: left;
  line-height: 16px;
  height: 16px;
  margin-top: 3px;
}

.SO-detailsIcon {
  height: 16px;
  display: inline;
  float: left;
  margin-right: 5px;
}

.SO-createSo {
  float: right;
  text-align: center;
  width: 200px;
  background-color: #FFCC00;
  border-radius: 5px;
  padding: 15px 40px;
  cursor: pointer;
  -webkit-box-shadow: 1px 1px 1px grey;
          box-shadow: 1px 1px 1px grey;
}



